import React from "react";
import ClearAllIcon from '@mui/icons-material/ClearAll';

function Note(props) {
    function handleClick() {
        props.onDelete(props.id);
    }
    return (
        <div className = "note">
        <h1>{props.title}</h1>
        <p>{props.content}</p>
        <button onClick={handleClick}><ClearAllIcon/></button>

    </div>
    );
}

export default Note;